@media only screen and (max-width: 1024px) {

    .app__navigation-bar {
        padding: 15px 70px;
    }

}

@media only screen and (max-width: 768px) {
    


}

@media only screen and (max-width: 425px) {

    .app__navigation-bar {
        padding: 15px 40px;
        flex-direction: column;

        h1 {
            margin-bottom: 16px;
        }
    }

    .city-search__container {
        width: 70%;
        
        .city-search__input {
            margin-bottom: 16px;
        }
    }

    .event-list {
        width: 90%;
        padding: 0;
    }

    .event-item__container {
        .event-data__collapsed {
            .event-data__flex {
                h1 {
                    font-size: 24px;
                    width: 70%;
                    line-height: 30px;
                }
                p {
                    width: 30%;
                    text-align: right;
                    font-size: 12px;
                }
            }
        }
    }
    
}